
.dokan-postbox .toggle-indicator:before {
    content: "\F142";
    display: inline-block;
    font: 400 20px/1 dashicons;
    speak: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-decoration: none!important
}
.dokan-postbox.closed .toggle-indicator:before {
    content: "\F140";
}
.dokan-postbox {
    position: relative;
}
.dokan-postbox h2.hndle {
    font-size: 14px;
    padding: 8px 12px;
    margin: 0;
    line-height: 1.4;
}

.dokan-loader {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
}
.dokan-loader div {
    position: absolute;
    border: 4px solid #FF5722;
    opacity: 1;
    border-radius: 50%;
    animation: dokan-loader 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.dokan-loader div:nth-child(2) {
    animation-delay: -0.5s;
}
@keyframes dokan-loader {
0% {
        top: 28px;
        left: 28px;
        width: 0;
        height: 0;
        opacity: 1;
}
100% {
        top: -1px;
        left: -1px;
        width: 58px;
        height: 58px;
        opacity: 0;
}
}
